webpackJsonp([71292731132602],{

/***/ 567:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<ul>\n<li>\n<p><a href=\"#configuring-security\">Configuring Security</a></p>\n<ul>\n<li><a href=\"#default-security-settings-for-karaf-containers\">Default Security Settings for Karaf containers</a></li>\n<li><a href=\"#example-customize-the-allowed-roles\">Example: Customize the allowed roles</a></li>\n<li><a href=\"#default-security-settings-for-web-containers\">Default Security Settings for web containers</a></li>\n<li><a href=\"#configuring-or-disabling-security-in-web-containers\">Configuring or disabling security in web containers</a></li>\n<li><a href=\"#configuring-security-in-apache-tomcat\">Configuring security in Apache Tomcat</a></li>\n<li><a href=\"#configuring-security-in-jetty\">Configuring security in Jetty</a></li>\n<li><a href=\"#keycloak-integration\">Keycloak Integration</a></li>\n</ul>\n</li>\n<li>\n<p><a href=\"#configuration-properties\">Configuration Properties</a></p>\n<ul>\n<li><a href=\"#osgi-configuration\">OSGi configuration</a></li>\n<li><a href=\"#jolokia-configuration\">Jolokia configuration</a></li>\n</ul>\n</li>\n</ul>\n<h2 id=\"configuring-security\"><a href=\"#configuring-security\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Configuring Security</h2>\n<p>Hawtio enables security out of the box depending on the container it is running within. There are two types of container:</p>\n<ul>\n<li><a href=\"#default-security-settings-for-karaf-containers\">Karaf (OSGi) containers</a></li>\n<li><a href=\"#default-security-settings-for-web-containers\">Web containers</a></li>\n</ul>\n<h3 id=\"default-security-settings-for-karaf-containers\"><a href=\"#default-security-settings-for-karaf-containers\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Default Security Settings for Karaf containers</h3>\n<p>By default the security in Hawtio uses these system properties when running in Apache Karaf containers (Karaf, ServiceMix, Red Hat Fuse) which you can override:</p>\n<div class=\"table-responsive\">\n<table class=\"table table-striped table-condensed table-hover\">\n  <thead>\n  <tr>\n    <th>Name</th>\n    <th>Default</th>\n    <th>Description</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr>\n    <td>\n      hawtio.authenticationEnabled\n    </td>\n    <td>\n      true\n    </td>\n    <td>\n      Whether or not security is enabled\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.realm\n    </td>\n    <td>\n      karaf\n    </td>\n    <td>\n      The security realm used to login\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.role or hawtio.roles\n    </td>\n    <td>\n      admin,manager,viewer\n    </td>\n    <td>\n      The user role or roles required to be able to login to the console. Multiple roles to allow can be separated by a comma. Set to <code>*</code> or an empty value to disable role checking when Hawtio authenticates a user.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.rolePrincipalClasses\n    </td>\n    <td>\n      org.apache.karaf.jaas.boot.principal.RolePrincipal,\n      org.apache.karaf.jaas.modules.RolePrincipal,\n      org.apache.karaf.jaas.boot.principal.GroupPrincipal\n    </td>\n    <td>\n      Fully qualified principal class name(s). Multiple classes can be separated by a comma. Set to an empty value to disable role checking when Hawtio authenticates a user.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.noCredentials401\n    </td>\n    <td>\n      false\n    </td>\n    <td>\n      Whether to return HTTP status 401 when authentication is enabled, but no credentials has been provided. Returning 401 will cause the browser popup window to prompt for credentials. By default this option is <code>false</code>, returning HTTP status 403 instead.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.authenticationContainerDiscoveryClasses\n    </td>\n    <td>\n      io.hawt.web.tomcat.TomcatAuthenticationContainerDiscovery\n    </td>\n    <td>\n        List of used <code>AuthenticationContainerDiscovery</code> implementations separated by comma. By default there is just <code>TomcatAuthenticationContainerDiscovery</code>, which is used to authenticate users on Tomcat from <code>tomcat-users.xml</code> file. Feel free to remove it if you want to authenticate users on Tomcat from configured JAAS login module or feel free to add more classes of your own.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.authenticationContainerTomcatDigestAlgorithm\n    </td>\n    <td>\n      NONE\n    </td>\n    <td>\n      When using the Tomcat <code>tomcat-users.xml</code> file, passwords can be hashed instead of plain text.  Use this to specify the digest algorithm; valid values are <code>NONE</code> <code>MD5</code> <code>SHA</code> <code>SHA-256</code> <code>SHA-384</code> <code>SHA-512</code>.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.tomcatUserFileLocation\n    </td>\n    <td>\n      conf/tomcat-users.xml\n    </td>\n    <td>\n        Specify an alternative location for the <code>tomcat-users.xml</code> file. e.g. <code>-Dhawtio.tomcatUserFileLocation=/production/userlocation/</code>\n     </td>\n  </tr>\n  </tbody>\n</table>\n</div>\n<p>Changing these values is often application server specific. Usually the easiest way to get Hawtio working in your container is to ensure you have a new user with the required role (by default its the <code>admin</code> role).</p>\n<h4 id=\"example-customize-the-allowed-roles\"><a href=\"#example-customize-the-allowed-roles\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Example: Customize the allowed roles</h4>\n<p>Hawtio reads its values in form of system properties. To define them on Karaf:</p>\n<pre><code>dev:system-property hawtio.roles my_organization_admin\n# restart Hawtio bundle\nrestart io.hawt.hawtio-war\n</code></pre>\n<p>Now only users with the <code>my_organization_admin</code> role will be allowed to login in Hawtio.</p>\n<p>To add the <code>my_organization_admin</code> role to the <code>admin</code> user on Karaf:</p>\n<pre><code>jaas:manage --realm karaf\njaas:roleadd admin my_organization_admin\njaas:update\n</code></pre>\n<h3 id=\"default-security-settings-for-web-containers\"><a href=\"#default-security-settings-for-web-containers\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Default Security Settings for web containers</h3>\n<p>For web containers, Hawtio uses the following system properties:</p>\n<div class=\"table-responsive\">\n<table class=\"table table-striped table-condensed table-hover\">\n  <thead>\n  <tr>\n    <th>Name</th>\n    <th>Default</th>\n    <th>Description</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr>\n    <td>\n      hawtio.authenticationEnabled\n    </td>\n    <td>\n      false\n    </td>\n    <td>\n      Whether or not security is enabled\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.realm\n    </td>\n    <td>\n      *\n    </td>\n    <td>\n      The security realm used to login\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.role or hawtio.roles\n    </td>\n    <td>\n    </td>\n    <td>\n      The user role or roles required to be able to login to the console. Multiple roles to allow can be separated by a comma. Set to <code>*</code> or an empty value to disable role checking when Hawtio authenticates a user.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.rolePrincipalClasses\n    </td>\n    <td>\n    </td>\n    <td>\n      Fully qualified principal class name(s). Multiple classes can be separated by a comma.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.noCredentials401\n    </td>\n    <td>\n      false\n    </td>\n    <td>\n      Whether to return HTTP status 401 when authentication is enabled, but no credentials has been provided. Returning 401 will cause the browser popup window to prompt for credentials. By default this option is <code>false</code>, returning HTTP status 403 instead.\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.authenticationContainerDiscoveryClasses\n    </td>\n    <td>\n      io.hawt.web.tomcat.TomcatAuthenticationContainerDiscovery\n    </td>\n    <td>\n        List of used <code>AuthenticationContainerDiscovery</code> implementations separated by comma. By default there is just <code>TomcatAuthenticationContainerDiscovery</code>, which is used to authenticate users on Tomcat from <code>tomcat-users.xml</code> file. Feel free to remove it if you want to authenticate users on Tomcat from configured JAAS login module or feel free to add more classes of your own.\n    </td>\n  </tr>\n  </tbody>\n</table>\n</div>\n<h4 id=\"configuring-or-disabling-security-in-web-containers\"><a href=\"#configuring-or-disabling-security-in-web-containers\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Configuring or disabling security in web containers</h4>\n<p>Set the following JVM system property to enable security:</p>\n<pre><code>hawtio.authenticationEnabled=true\n</code></pre>\n<p>Or adjust the <code>web.xml</code> file and configure the <code>&#x3C;env-entry></code> element, accordingly.</p>\n<h4 id=\"configuring-security-in-apache-tomcat\"><a href=\"#configuring-security-in-apache-tomcat\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Configuring security in Apache Tomcat</h4>\n<p>Set the following <code>CATALINA_OPTS</code> environment variable:</p>\n<pre><code>export CATALINA_OPTS=-Dhawtio.authenticationEnabled=true\n</code></pre>\n<p>Then Hawtio will auto-detect that it is running in Apache Tomcat, and use its user data file (<code>conf/tomcat-users.xml</code>) for security.</p>\n<p>For example to set up a new user named <code>scott</code> with password <code>tiger</code>, then edit the file <code>conf/tomcat-users.xml</code> to include:</p>\n<pre><code>&#x3C;user username=\"scott\" password=\"tiger\" roles=\"tomcat\"/>\n</code></pre>\n<p>Then you can login to Hawtio with the username <code>scott</code> and password <code>tiger</code>.</p>\n<p>If you only want users of a special role to be able to login Hawtio then you can set the role name in the <code>CATALINA_OPTS</code> environment variable as shown:</p>\n<pre><code>export CATALINA_OPTS='-Dhawtio.authenticationEnabled=true -Dhawtio.role=manager'\n</code></pre>\n<p>Now the user must be in the <code>manager</code> role to be able to login, which we can set up in the <code>conf/tomcat-users.xml</code> file:</p>\n<pre><code>&#x3C;role rolename=\"manager\"/>\n&#x3C;user username=\"scott\" password=\"tiger\" roles=\"tomcat,manager\"/>\n</code></pre>\n<p>Note that if you still want to use your own login modules instead of <code>conf/tomcat-users.xml</code> file, you can do it by remove <code>TomcatAuthenticationContainerDiscovery</code> from\nsystem properties and point to <code>login.conf</code> file with your login modules configuration, something like:</p>\n<pre><code>export CATALINA_OPTS='-Dhawtio.authenticationEnabled=true -Dhawtio.authenticationContainerDiscoveryClasses= -Dhawtio.realm=hawtio -Djava.security.auth.login.config=$CATALINA_BASE/conf/login.conf'\n</code></pre>\n<p>Then you can configure JAAS in file <code>TOMCAT_HOME/conf/login.conf</code> (example of file below in <a href=\"#configuring-security-in-jetty\">Jetty section</a>).</p>\n<h4 id=\"configuring-security-in-jetty\"><a href=\"#configuring-security-in-jetty\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Configuring security in Jetty</h4>\n<p>To use security in Jetty you first have to set up some users with roles. To do that navigate to the <code>etc</code> folder of your Jetty installation and create the following file <code>etc/login.properties</code> and enter something like this:</p>\n<pre><code>scott=tiger, user\nadmin=CRYPT:adpexzg3FUZAk,admin,user\n</code></pre>\n<p>You have added two users. The first one named <code>scott</code> with the password <code>tiger</code>. He has the role <code>user</code> assigned to it. The second user <code>admin</code> with password <code>admin</code> which is obfuscated (see Jetty realms for possible encryption methods). This one has the <code>admin</code> and <code>user</code> role assigned.</p>\n<p>Now create the second file in the same directory called <code>login.conf</code>. This is the login configuration file.</p>\n<pre><code>hawtio {\n  org.eclipse.jetty.jaas.spi.PropertyFileLoginModule required\n  debug=\"true\"\n  file=\"${jetty.base}/etc/login.properties\";\n};\n</code></pre>\n<p>Next you have to change the Hawtio configuration:</p>\n<div class=\"table-responsive\">\n<table class=\"table table-striped table-condensed table-hover\">\n  <thead>\n  <tr>\n    <th>Name</th>\n    <th>Default</th>\n    <th>Description</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr>\n    <td>\n      hawtio.authenticationEnabled\n    </td>\n    <td>\n      true\n    </td>\n    <td>\n      Whether or not security is enabled\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.realm\n    </td>\n    <td>\n      hawtio\n    </td>\n    <td>\n      The security realm used to login\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.role or hawtio.roles\n    </td>\n    <td>\n    admin\n    </td>\n    <td>\n      The user role required to be able to login to the console\n    </td>\n  </tr>\n  <tr>\n    <td>\n      hawtio.rolePrincipalClasses\n    </td>\n    <td>\n    </td>\n    <td>\n      Fully qualified principal class name(s). Multiple classes can be separated by a comma.\n    </td>\n  </tr>\n  </tbody>\n</table>\n</div>\n<p>You have now enabled security for Hawtio. Only users with role <code>admin</code> are allowed.</p>\n<p>At last enable the JAAS module in Jetty. This is done by adding the following line to the <code>start.ini</code> which is located in the <code>jetty.base</code> folder:</p>\n<pre><code># Enable security via JAAS, and configure it\n--module=jaas\n</code></pre>\n<h3 id=\"keycloak-integration\"><a href=\"#keycloak-integration\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Keycloak Integration</h3>\n<p>Hawtio can now be integrated with <a href=\"https://www.keycloak.org\">Keycloak</a> for SSO authentication. See <a href=\"../keycloak-integration/\">Keycloak Integration Guide</a>.</p>\n<h2 id=\"configuration-properties\"><a href=\"#configuration-properties\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Configuration Properties</h2>\n<p>The following table contains configuration settings for various Hawtio plugins.</p>\n<table class=\"table\">\n  <thead>\n    <tr>\n      <th>System Property</th>\n      <th>Default</th>\n      <th>Description</th>\n    </tr>\n  </thead>\n  <tbody>\n    <tr>\n      <td>hawtio.sessionTimeout</td>\n      <td></td>\n      <td>The maximum time interval, in seconds, that the servlet container will keep this session open between client accesses. If this option is not configured, then Hawtio uses the default session timeout of the servlet container.</td>\n    </tr>\n    <tr>\n      <td>hawtio.activemq.verbose.tree</td>\n      <td><code>false</code></td>\n      <td>Whether or not verbose ActiveMQ details are rendered in the tree in Hawtio. This ensures situations when ActiveMQ will constantly add/remove same set of mbeans for a client connection because the client is not using pooled connections or using XA transactions without caching the consumer. In situations like these ActiveMQ keeps changing the mbeans which would cause the Hawtio web console to trigger an update in the tree, which makes using the web console more sluggish. Therefore this is filtered out by default. This option can be set to <code>true</code> to restore old behavior.</td>\n    </tr>\n    <tr>\n      <td>hawtio.proxyWhitelist</td>\n      <td><code>localhost, 127.0.0.1</code></td>\n      <td>Comma-separated whitelist for target hosts that the <a href=\"https://github.com/hawtio/hawtio-jmx\">hawtio-jmx</a> Connect plugin can connect to via <code>ProxyServlet</code>. All hosts that are not listed in this whitelist are denied to connect for security reasons. This option can be set to <code>*</code> to restore old behavior and whitelist all hosts. Prefixing an element of the list with <code>\"r:\"</code> allows to define a regexp (example: <code>localhost,r:myservers[0-9]+.mydomain.com</code>)</td>\n    </tr>\n    <tr>\n      <td>hawtio.localAddressProbing</td>\n      <td><code>true</code></td>\n      <td><b>Hawtio 2.5.0</b> - Whether local address probing for proxy whitelist is enabled or not upon startup. Set this property to <code>false</code> to disable it.</td>\n    </tr>\n  </tbody>\n</table>\n<h3 id=\"osgi-configuration\"><a href=\"#osgi-configuration\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>OSGi configuration</h3>\n<p>Simply update the blueprint configuration values in OSGi config admim as you would any OSGi blueprint bundles. On OSGi all the Hawtio Java modules use OSGi blueprint.</p>\n<h3 id=\"jolokia-configuration\"><a href=\"#jolokia-configuration\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Jolokia configuration</h3>\n<p>The Jolokia agent is deployed automatically with <code>io.hawt.web.JolokiaConfiguredAgentServlet</code> that extends Jolokia native <code>org.jolokia.http.AgentServlet</code> class, defined in <code>hawtio-war/WEB-INF/web.xml</code>.</p>\n<p>To customize Jolokia Servlet configuration, according to the parameters that it supports and that are defined in the <a href=\"https://jolokia.org/reference/html/agents.html#agent-war-init-params\">Jolokia documentation</a>, you have to pass them as Java System properties prefixed with <code>jolokia.</code>. E.g.:</p>\n<pre><code>-Djolokia.policyLocation=file:///opt/hawtio/my-jolokia-access.xml\n</code></pre>\n<p><strong>NOTE</strong> The parameter <code>restrictorClass</code> is already used in <code>hawtio-war/WEB-INF/web.xml</code> to implement role-based access control (RBAC) for Jolokia invocations. If you want to use your own Jolokia restrictor, make sure to extend <code>io.hawt.web.RBACRestrictor</code> to implement your own restrictor class. Otherwise, part of Hawtio's RBAC functions will be lost.</p>","frontmatter":{"title":"Configuration"}}},"pathContext":{"slug":"/docs/configuration/"}}

/***/ })

});


